@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Staatliches&display=swap);
* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}
body {
  font-family: "Open Sans", serif;
  min-width: 320px;
  width: auto !important;
  width: 100%;
}
form {
  display: flex;
  justify-content: center;
}
#bgImg {
  position: fixed;
  right: 0;
  bottom: 50px;
  -webkit-animation: focus-in-contract 1s ease-out forwards;
          animation: focus-in-contract 1s ease-out forwards;
  opacity: 0;
  height: 40vh;
  z-index: -99;
}
.results {
  margin-left: 10%;
  margin-right: 10%;
  display: flex;
  flex-wrap: wrap;
  z-index: -99;

  justify-content: space-around;
}

.results::-webkit-scrollbar {
  display: none;
}

.results h1 {
  margin-top: 5px;
  font-size: 22px;
}
.resultstitle h2 {
  margin-top: 150px;
  margin-left: auto;
  text-align: center;
  letter-spacing: 0.15em;
}
.info-container h1 {
  font-size: 16px;
  text-align: center;
  margin: 5px;
}
.book-card {
  margin-bottom: 1em;
  background-color: #fff;
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);
  color: #4a4a4a;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15px;
}

a {
  text-decoration: none;
}
a:visited,
a:link {
  color: black;
}

.book-thumbnail {
  box-shadow: 2px 2px 8px 2px rgb(70, 70, 70);
  margin-top: 10px;
  max-height: 150px;
  max-width: 150px;
}
.desc {
  text-align: center;
  margin-bottom: 5px;
  margin-top: 5px;
  font-style: normal;
  color: #15477a;
  font-size: smaller;
}
.summary {
  margin: 10px;
  font-size: smaller;
}
.btn-info {
  margin-top: 10px;
  box-shadow: inset 0px 0px 15px 3px #23395e;
  background: linear-gradient(to bottom, #2e466e 5%, #415989 100%);
  background-color: #2e466e;
  border-radius: 17px;
  border: 1px solid #1f2f47;
  cursor: pointer;
  color: #ffffff;
  font-size: 14px;
  padding: 8px 14px;
  text-shadow: 0px 1px 0px #263666;
  margin-bottom: 8px;
}
.btn-info:hover {
  background: linear-gradient(to bottom, #8193b8 5%, #395380 100%);
  background-color: #415989;
}
.btn-info:active {
  position: relative;
  top: 1px;
}

#topPageScroll {
  display: none;
  position: fixed;
  bottom: 40px;
  right: 30px;
  z-index: 99;
  font-size: 18px;
  border: none;
  outline: none;
  background-color: #23395e;
  color: white;
  cursor: pointer;
  padding: 15px;
  border-radius: 4px;
}

#topPageScroll:hover {
  background-color: #555;
}

.icon {
  float: left;
  height: 30px;
  margin-left: 10px;
  color: #ffffff;
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

footer {
  display: block;
  text-align: center;
  position: relative;
  bottom: 0;
  width: 100%;
  position: fixed;
  height: 50px;
  background-color: #15477a;
  justify-content: space-around;
  align-items: center;
  -webkit-animation: focus-in-contract 0.6s ease-out forwards;
          animation: focus-in-contract 0.6s ease-out forwards;
}
.footer p {
  color: #fff;
  margin-top: 10px;
}

@media screen and (min-width: 768px) {
  footer {
    display: flex;
    height: 75px;
    border: #fff solid 2px;
  }
  .footer {
    margin-left: 15px;
    margin-right: 15px;
  }
  .icon {
    height: 40px;
    margin-right: 10px;
  }

  .results {
    grid-row-gap: 5px;
    row-gap: 5px;
    grid-column-gap: 0;
    -webkit-column-gap: 0;
            column-gap: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  .info-container {
    margin: 20px;
  }

  .book-card {
    margin: 5px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 20%;
    page-break-inside: avoid;
    -webkit-column-break-inside: avoid;
            break-inside: avoid;
    padding: 10px;
    border-radius: 10px;
    display: flex;
    flex: 1 1 auto;
    margin-bottom: 10px;
    border-radius: 10px;
  }
  .resultstitle h2 {
    margin-top: 280px;
  }
  #bgImg {
    height: 60vh;
    position: fixed;
    right: 0;
    bottom: 10vh;
    -webkit-animation: focus-in-contract 1s ease-out forwards;
            animation: focus-in-contract 1s ease-out forwards;
    opacity: 0;
    z-index: -99;
  }
}

header {
  position: fixed;
  width: 100%;
  z-index: 99;
  background-color: #fff;
  height: 9rem;
  margin-top: 0;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    169deg,
    rgba(255, 255, 255, 1) 42%,
    rgba(200, 230, 255, 1) 64%,
    rgba(0, 106, 217, 1) 88%,
    rgba(0, 25, 122, 1) 100%
  );
}

.logo-img {
  height: 7.5vh;
  margin-top: 30px;
}

.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.header h1 {
  margin-top: 20px;
  color: black;
  font-family: "Staatliches";
  font-size: 45px;
  font-weight: 100;
}
.slogan {
  display: flex;
  justify-content: center;
  color: black;
  font-family: "Montserrat";
  -webkit-animation: pop-in 0.6s ease-out forwards;
          animation: pop-in 0.6s ease-out forwards;
  opacity: 0;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  text-transform: uppercase;
  font-size: 12px;
  margin-left: 75px;
  margin-top: -5px;
}

.search {
  z-index: 99;
  position: fixed;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: space-between;
  margin-top: 95px;
  width: 70%;
}

input {
  padding-left: 10px;
  height: calc(2.25rem + 2px);
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  letter-spacing: 0.12em;
  font-size: 0.8rem;
  margin-right: 1em;
  -webkit-animation: focus-in-contract 0.6s ease-out forwards;
          animation: focus-in-contract 0.6s ease-out forwards;
  opacity: 0;

  flex: 1 0;
}

.btn-submit {
  display: block;
  flex-grow: 1;
  order: 0;
  -webkit-animation-delay: 1.2s;
          animation-delay: 1.2s;
}
.input-box {
  display: block;
  flex-grow: 3;
  order: 0;
}

.btn-submit {
  height: calc(2.25rem + 2px);
  color: #495057;
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 0.3rem;
  -webkit-animation: focus-in-contract 0.6s ease-out forwards;
          animation: focus-in-contract 0.6s ease-out forwards;
  opacity: 0;
}

.btn-submit:hover {
  color: #fff;
  background: linear-gradient(to bottom, #8193b8 5%, #395380 100%);
  background-color: #415989;
  border: none;
}

.header,
input {
  -webkit-animation-name: focus-in-contract;
          animation-name: focus-in-contract;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-direction: normal;
          animation-direction: normal;
}
@-webkit-keyframes focus-in-contract {
  0% {
    letter-spacing: 1em;
    -webkit-filter: blur(12px);
            filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-filter: blur(0);
            filter: blur(0);
    opacity: 1;
  }
}
@keyframes focus-in-contract {
  0% {
    letter-spacing: 1em;
    -webkit-filter: blur(12px);
            filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-filter: blur(0);
            filter: blur(0);
    opacity: 1;
  }
}

@-webkit-keyframes pop-in {
  0% {
    opacity: 0;
    transform: translateY(-2rem) scale(1.8);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}

@keyframes pop-in {
  0% {
    opacity: 0;
    transform: translateY(-2rem) scale(1.8);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}

@media screen and (min-width: 768px) {
  header {
    height: 16rem;
  }

  .header h1 {
    font-size: 100px;
    margin-left: 15px;
  }

  .logo-img {
    height: 110px;
  }

  .slogan {
    font-size: 1.5rem;
    margin-left: 15rem;
  }

  .search {
    z-index: 99;
    width: 60%;
    margin-top: 180px;
  }
}

